<template>
  <div id="app" :style="{backgroundImage: 'url('+imgUrl+')'}">
    <todoList></todoList>
  </div>
</template>

<script>

import todoList from './components/todoList.vue'

const jsonp = require('jsonp');

export default {
  name: 'app',
  data(){
    return{
      imgUrl:""
    }
  },
  components: {
    todoList
  },
  created(){

      jsonp('https://bing.ioliu.cn/v1/rand?w=1920&h=1200', null, (err, res) => {

      if (err) {
        console.error(err);
      } else {
        this.imgUrl = res.data.url;
      }

    });

  }
}
</script>

<style lang='less' scoped>
  #app{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;    
    background: url('./assets/images/bg.jpg') 100% 100% no-repeat;
    background-size: cover;     
    z-index: -1;   
    // -webkit-filter: blur(5px);
    // filter: blur(15px);
    // transform: scale(1.2);
  }
</style>
